*{
    font-family: Arial, Helvetica, sans-serif;
  }

.ash{
  font-size: 22px;
  font-weight: bold;
  margin: 0 5px;
  text-align: center;
}

.modal_active{
    background-color: rgb(113, 123, 182);
    width: 90%;
    height:80%;
    border-radius: 20px;
    display: grid;
    grid-template-rows: repeat(8, 1fr);
    grid-auto-flow: column;
    grid-auto-columns: minmax(0, 1fr);
    grid-column-gap: 0.3em;
    place-items: center;
}

.modal_active_scores{
  background-color: rgb(113, 123, 182);
    width: 90%;
    height:90%;
    border-radius: 20px;
    display: grid;
    grid-template-rows: repeat(9, 1fr);
    grid-auto-flow: column;
    grid-auto-columns: minmax(0, 1fr);
    grid-column-gap: 0.3em;
    place-items: center;
}

.scores{
  font-size: 24px;
  font-weight: bold;
}
  
  .calloutcontent{  
    height: 2.5em;
  width: 80%;
  background-color: rgb(79, 86, 99);
  color:rgb(175, 171, 171);
  border-radius: 10px;
  padding-left: 5%;
  padding-right: 5%;
  font-size: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  }

  .modal_content_callout{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items:center;
    background-color: rgb(113, 123, 182);
    width: 91%;
    height:85%;
    border-radius: 20px;
    margin-top: 10vh;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
    max-width: 420px;
  }

.modal_add,.modal_rules,.modal_callout{
    position: fixed;
    z-index: 1;
    height: 100%;
    width: 100%;
    left:0;
    top: 0;
    background-color: rgba(24, 64, 116, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
.modal_content_add {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items:center;
    background-color: rgb(113, 123, 182);
    width: 90%;
    height:25%;
    border-radius: 20px;
    max-width: 420px;

  }

  .modal_content_rules {
    display: flex;
    flex-direction: column;
    align-items:center;
    background-color: rgb(113, 123, 182);
    width: 90%;
    height:auto;
    border-radius: 20px;
    max-width: 420px;
  }
 

  .modal_content_rules > :not(.tips){
    margin: 15px;
    text-align: left;
  }

  .tips{
    margin: 15px;
    text-align: center;
  }

  ul{
    padding-left: 15px;
    padding-right: 15px;
    text-align: left;
    margin: 0;
  }


input[type=text] {
    background-color: #2c3369;
    color: rgb(182, 192, 196);
    border: none;
    border-radius: 5px;
    height: 5dvh;
    font-size: 16px;
    padding: 0px 10px;
    text-align: center;
    width: 240px;
}

input[type=text]:focus {
    outline:none;
}

.fancyButton{
    background-color: #2c3369;
    border: none;
    color: rgb(182, 192, 196);
    padding: 5px 20px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    border-radius:5px;
    
}

.fancyButton_add{
    background-color: #2c3369;
    border: none;
    color: rgb(182, 192, 196);
    padding: 5px 20px;
    text-decoration: none;
    font-size: 16px;
    border-radius:5px;
}

.error{
  visibility: hidden;
  color:rgb(163, 7, 7);
  height: 1em;
  font-size: 14px;
  display: flex;
  align-items: center;
  margin:0 10px;
  text-align: center;
}

form{
  height: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

