*{
    font-family: Arial, Helvetica, sans-serif;
  }

.home{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100dvh;
    background-image: url('images/background_v2.svg');
    background-position: center;
    background-size:cover;
    background-attachment: fixed;  
    max-width: 480px;
    background-color: white;
  }

  .wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(110, 113, 116);
  }
  
  .logo{
    background-image: url('images/logo_v3.svg');
    background-position: center;
    background-repeat: no-repeat;
    width: 85%;
    height: 7dvh;
    background-size:contain;
    
  }

  .playertable{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 85%;
    height: 83dvh;
  }  
  
  .players{
    width: 100%;
    height: 85%;
    display: grid;
    grid-template-rows: repeat(10, 1fr);
    grid-auto-flow: column;
    grid-auto-columns: minmax(0, 1fr);
    grid-column-gap: 0.3em;
  }
  
  .player{
    color:rgb(193, 220, 231);
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 2em;
    background-color: rgb(51, 47, 47);
    border-radius: 10px;
    font-size: 20px;
  }
  
  .name{
    max-width: 75%;
    margin: 5px;
    white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  }
   .addplayer{
    aspect-ratio: 1/1;
    height: 7dvh;
    background-image: url('images/plus_button.svg');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
  }
  
  .removeplayer{
    aspect-ratio: 1/1;
    height: 75%;
    margin: 3px;
    display: flex;
    justify-content: center;
    border-radius: 50%;
    background-image: url('images/x_button.svg');
    background-position: center;
    background-size: contain;
  }
  
  .start_rules{
    display: flex;
    width: 100%;
    height: 10dvh;
    justify-content: space-evenly;
  }

  .rulesBtn{
    display: flex;
    color:rgb(193, 220, 231);
    background-color: rgb(57, 78, 168);
    justify-content: center;
    align-items: center;
    font-size: 18px;
    height: 95%;
    width: 40%;
    border-radius: 10px;
    padding: 0 5px;
  }

  .start{
    height: 95%;
    width: 40%;
    color:rgb(193, 220, 231);
    padding: 0 5px;
    border-radius: 10px;
    background-color: rgb(20, 80, 110);
    display: flex;
    align-items: center;
  }
  

  .start > *{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    text-align: center;
    height: 100%;
    width: 100%;
    text-decoration: none;
  }

  